            return row.address;
        },
        handleCurrentChange(cpage) {
            this.currpage = cpage;
        },
        handleSizeChange(psize) {
            this.pagesize = psize;
        },
        getValue(value){
            // console.log(value)
            this.tableData = value
        },
        getDetail(data){
            var _this = this
            console.log(data)
            if(data.num1 == '8'){
                var data = {reg:'gzyxssl',style:'2'}
            }else if(data.num1 == '9'){
                var data = {reg:'gzyssb',style:'2'}
            }else if(data.num1 == '3'){
                var data = {reg:'xzzyjyltbt',style:'2'}
            }else if(data.num1 == '4'){
               var data = {reg:'yjsdkjyltbt',style:'2'}
            }else if(data.num1 == '5'){
                var data = {reg:'sjbysmydtbt',style:'2'}
                var rotate = '0'
            }else if(data.num1 == '1'){
                var data = {reg:'cxxsbltbt',style:'2'}
            }
            else if(data.num1 == '2'){
                var data = {reg:'yjbysszhqtbt',style:'2'}
            }
            var _this = this
            $.ajax({
                type: "post",
                url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
                data: data,
                dataType: "json",
                success: function (res) {
                    console.log(res)
                    _this.$router.push({
                        path:'/detail',
                        name:'detail',
                        params:{
                            title: res.title,
                            dataCount1: res.dataCount,
                            dataCount2: res.zyxstbt,
                            param:res.param1,
                            rotate:rotate
                        }
                    })
                },
                error:function(err){
                    console.log(err)
                }
            });
        }
    },
    mounted() {
        this.windowWidth = document.documentElement.offsetWidth;
        console.log(this.windowWidth)
        var _this = this
        $.ajax({
            type: "post",
            url: _this.baseUrl+"/ydjxzg/hx/xsgl.action",
            data: {reg:'zzzgdbxln',style:'1'},
            dataType: "json",
            success: function (res) {
                console.log(res)
                _this.data1.num=res.cxxsbl.bl2
                _this.data2.num=res.yjbysszhql.bl2
                _this.data3.num=Math.round(res.bysjyl.zj*100)+'%'
                _this.data4.num=Math.round(res.bysdkjyl.zj*100)+'%'
                _this.data5.num=Math.round(res.sjbysmyd.zj)
                _this.data6.num=Math.round(res.yjstzdbl.zj*100)+'%'
                _this.data7.num=Math.round(res.xxsjwjl.zj*100)+'%'
                _this.data8.num=Math.round(res.jsagefb.zcount)+'人'
                _this.data9.num=Math.round(res.zszysl.zcount)+'个'

                if(res.sxjyxszb){             //办学经费饼图
                    //_this.baroption6.param.push(res.sxjyxszb[0].title)
                    for(var i = 1;i < res.sxjyxszb.length;i++){
                        //_this.pielegend.push(res.sxjyxszb[i].mc)
                        _this.baroption3.series[0].data[i-1].value = res.sxjyxszb[i].zcount
                        _this.baroption3.series[0].data[i-1].name = res.sxjyxszb[i].mc
                    }
                     //_this.baroption6.param.push(data.param)
                     //_this.baroption6.title = data.title
                     _this.baroption3.series[0].name = '人'
                    _this.baroption3.legend.data = _this.pielegend
                }
                if(res.gzyxssl){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption1.param.push(res.gzyxssl[0].param1)
                    for(var i = 1;i < res.gzyxssl.length;i++){
                        if(res.gzyxssl[i].mc==null){
                            _this.xData.push('暂无专业')
                        }else{
                            _this.xData.push(res.gzyxssl[i].mc)
                        }
                        _this.yData.push(res.gzyxssl[i].zcount)
                    }
                    _this.baroption4.yAxis[0].name = '(人)'
                    _this.baroption4.xAxis[0].data = _this.xData;
                    _this.baroption4.series[0].data = _this.yData;
                }
                if(res.bysjydqfb){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption1.param.push(res.gzyxssl[0].param1)
                    for(var i = 1;i < res.bysjydqfb.length;i++){
                        if(res.bysjydqfb[i].mc==null){
                            _this.xData.push('暂无')
                        }else{
                            _this.xData.push(res.bysjydqfb[i].mc)
                        }
                        _this.yData.push(res.bysjydqfb[i].zcount)
                    }
                    _this.baroption5.yAxis[0].name = '(人)'
                    _this.baroption5.xAxis[0].data = _this.xData;
                    _this.baroption5.series[0].data = _this.yData;
                }
                if(res.glshfwsl){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption1.param.push(res.gzyxssl[0].param1)
                    for(var i = 1;i < res.glshfwsl.length;i++){
                        if(res.glshfwsl[i].mc==null){
                            _this.xData.push('暂无')
                        }else{
                            _this.xData.push(res.glshfwsl[i].mc)
                        }
                        _this.yData.push(res.glshfwsl[i].zcount)
                    }
                    _this.baroption6.yAxis[0].name = '(人)'
                    _this.baroption6.xAxis[0].data = _this.xData;
                    _this.baroption6.series[0].data = _this.yData;
                }
                if(res.dygzdwtbt){
                    _this.xData = []
                    _this.yData = []
                    //_this.baroption1.param.push(res.stulsbht[0].param1)
                    for(var i = 1;i < res.dygzdwtbt.length;i++){
                        if(res.dygzdwtbt[i].mc==null){
                            _this.xData.push('其它')
                        }else{
                            _this.xData.push(res.dygzdwtbt[i].mc)
                        }
                        _this.yData.push(res.dygzdwtbt[i].zcount)
                    }
                    _this.baroption7.yAxis[0].name = '(人)'
                    _this.baroption7.xAxis[0].data = _this.xData;
                    _this.baroption7.series[0].data = _this.yData;
                }
                if(res.dygzjg){             //德育工作机构一览表
                        for(var i = 1;i < res.dygzjg.length;i++){
                       _this.tableData1.push(res.dygzjg[i])
                    }              
                }
                if(res.dykcksylb){             //
                        for(var i = 1;i < res.dykcksylb.length;i++){
                       _this.tableData2.push(res.dykcksylb[i])
                    }              
                }
                if(res.cxjxjscg){             //
                        for(var i = 1;i < res.cxjxjscg.length;i++){
                       _this.tableData3.push(res.cxjxjscg[i])
                    }              
                }
                if(res.cxjxjscg){             //素质教育标志性成果一览表
                        for(var i = 1;i < res.cxjxjscg.length;i++){
                       _this.tableData4.push(res.cxjxjscg[i])
                    }              
                }
                if(res.shsjhdcg){             //素质教育标志性成果一览表
                        for(var i = 1;i < res.shsjhdcg.length;i++){
                       _this.tableData5.push(res.shsjhdcg[i])
                    }              
                }





                
                _this.char = true          //写在成功回调的最下面
            },
            error:function(err){
                console.log(err)
            }
        });
    },
}
</script>

